/*
 * Pagination
 * ----------------------
 */

.page-link {
	background-color: #46456f;
	border: 1px solid rgba(255, 255, 255, 0.1);
	&:hover {
		background-color: rgba(70, 127, 207, 0.06);
		border: 1px solid rgba(255, 255, 255, 0.1);
		box-shadow: none;
	}
}
.page-item {
	&.active .page-link {
		z-index: 1;
		color: #fff;
		background-color: #736cc7;
		border-color: #7673e6;
	}
	&.disabled .page-link {
		color: #ced4da;
		background-color: #fff;
		border-color: rgba(255, 255, 255, 0.1);
	}
	.page-link {
		color: #7673e6;
	}
}
table.dataTable {
	td, th {
		-webkit-box-sizing: content-box;
		box-sizing: content-box;
	}
	&.nowrap {
		th, td {
			white-space: nowrap;
		}
	}
}
/*
 * File Upload
 * ----------------------
 */

.files {
	input {
		outline: 2px dashed rgba(255, 255, 255, 0.1);
		-webkit-transition: outline-offset .15s ease-in-out, background-color .15s linear;
		transition: outline-offset .15s ease-in-out, background-color .15s linear;
		&:focus {
			outline: 2px dashed rgba(255, 255, 255, 0.1);
			outline-offset: -10px;
			-webkit-transition: outline-offset .15s ease-in-out, background-color .15s linear;
			transition: outline-offset .15s ease-in-out, background-color .15s linear;
		}
	}
	&:after {
		background: #343350;
		color: #c5c5f3;
	}
}
.color input {
	background-color: #343350;
}
.files:before {
	color: #c5c5f3;
}
